.cabinet {
    &__container {
        display: flex;
        flex-direction: column;
    }

    &__header {
        display: flex;
        @media(max-width: $tablet_large) {
            flex-wrap: wrap;
        }
    }

    &__section {
        margin-bottom: 30px;
        
        &--slider {
            width: 48%;
            max-width: 870px;
            margin-right: auto;
        }

        &--about {
            width: 50%;
            max-width: 930px;
            margin-right: 0;
        }

        @media(max-width: $tablet_large) {
            &--slider {
                width: 100%;
                max-width: 100%
            }
    
            &--about {
                width: 100%;
                max-width: 100%
            }
        }
    }
}